<template>
  <div class="mdl-grid order">
    <div class="mdl-cell--5-col-desktop	mdl-cell--4-col-phone client">
      <client-view
        :client="clientParams"
        :data="clientData"
        :action="clientAction"
        :status="orderStatus"
        :fk="'vendor'"
        @clientSelected="clientParams=$event"
        ref="client"
      >
      </client-view>
    </div>
    <div class="mdl-cell--7-col-desktop	mdl-cell--4-col-phone">
      <div>
        <line-items-view
          :items="lineItemParams"
          :data="productData"
          :action="productAction"
          :field="name"
          :fk="productFk"
          @selectedLineItems="lineItemParams=$event"
          @totalSelectedItemsAmount="total=$event"
          ref="items"
        ></line-items-view>
      </div>
    </div>
    <div class="mdl-cell--8-col-desktop	mdl-cell--4-col-phone transaction">
      <transaction-view
        @selectedTransaction="transactionParams=$event"
        @amountEntered ="amount=$event"
        :transactions="orderParams.transactions"
        :client="client"
        ref="transaction"
      ></transaction-view>
    </div>
    <div class="mdl-cell--4-col-desktop	mdl-cell--4-col-phone total">
      <div class="md-list">
        <div class="mdl-list__item">
          <div class="mdl-list__item-primary-content">
            Amount
          </div>
          <div class="mdl-list__item-secondary-content">
            {{total}}
          </div>
        </div>
         <div class="mdl-list__item">
          <span class="mdl-list__item-primary-content">
            <div>
              <div class="mdl-textfield mdl-js-textfield mdl-textfield--floating-label" :class="{'errors': errors.has('sgst_percentage')}">
                <input v-validate="'required|decimal'" name="sgst_percentage" id="sgst_percentage" class="mdl-textfield__input" v-model="orderParams.sgst_percentage">
                <label class="mdl-textfield__label" for="sgst_percentage">SGST</label>
                <span class="mdl-textfield__error">{{ errors.first('sgst_percentage') }}</span>
              </div>
            </div>
          </span>
          <span class="mdl-list__item-secondary-content">
            {{sgstAmount}}
          </span>
        </div>
        <div class="mdl-list__item">
          <div class="mdl-list__item-primary-content">
            <div class="mdl-textfield mdl-js-textfield mdl-textfield--floating-label" :class="{'errors': errors.has('cgst_percentage')}">
              <input v-validate="'required|decimal'" name="cgst_percentage" id="cgst_percentage" class="mdl-textfield__input" v-model="orderParams.cgst_percentage">
              <label class="mdl-textfield__label" for="cgst_percentage">CGST</label>
              <span class="mdl-textfield__error">{{ errors.first('cgst_percentage') }}</span>
            </div>
          </div>
          <div class="mdl-list__item-secondary-content">
            {{cgstAmount}}
          </div>
        </div>
         <div class="mdl-list__item">
          <span class="mdl-list__item-primary-content">
            Pending Amount
          </span>
          <span class="mdl-list__item-secondary-content">
            {{pendingAmount}}
          </span>
        </div>
        <div class="mdl-list__item">
          <span class="mdl-list__item-primary-content">
            Total Amount
          </span>
          <span class="mdl-list__item-secondary-content">
            {{totalAmount}}
          </span>
        </div>
      </div>
    </div>
    <button
      type="button"
      class="mdl-button mdl-js-button mdl-button--raised mdl-js-ripple-effect mdl-button--colored custom-button--primary"
      @click="proceed"
    >
    Purchase
    </button>
  </div>
</template>
<script>
import { LineItemsView, ClientView, TransactionView } from '.'
import { ORDER_STATUS } from '@/constants'
import { reInitiateForm } from '@/helpers.js'

export default {
  name: 'order-view',
  props: {
    clientData: {
      type: Array,
      required: true
    },
    clientAction: {
      type: Function,
      required: true
    },
    clientFk: {
      type: String,
      required: true
    },
    productData: {
      type: Array,
      required: true
    },
    productAction: {
      type: Function,
      required: true
    },
    order: {
      type: Object
    },
    productFk: {
      type: String,
      required: true
    },
    name: {
      type: String,
      default: 'name'
    }
  },
  components: {
    LineItemsView,
    ClientView,
    TransactionView
  },
  watch: {
    amount (value) {
      this.calculatePendingAmpount()
    },
    totalAmount () {
      this.calculatePendingAmpount()
    }
  },
  computed: {
    orderParams () {
      return this.order || this.params
    },
    orderStatus () {
      return this.orderParams.status || ORDER_STATUS.initiate
    },
    clientParams: {
      get () {
        return this.orderParams
      },
      set (data) {
        this.orderParams.name = data.name
        this.orderParams.gst_number = data.gst_number
        this.orderParams.address = data.address
        this.orderParams[this.clientFk] = data[this.clientFk]
        this.client = data
        reInitiateForm()
      }
    },
    lineItemParams: {
      get () {
        return this.orderParams.line_items
      },
      set (data) {
        this.$set(this.orderParams, 'line_items', data)
      }
    },
    transactionParams: {
      get () {
        return this.orderParams.transactions
      },
      set (data) {
        this.$set(this.orderParams, 'transactions', data)
        reInitiateForm()
      }
    },
    sgstAmount () {
      return (this.total * this.orderParams.sgst_percentage) / 100
    },
    cgstAmount () {
      return (this.total * this.orderParams.cgst_percentage) / 100
    },
    totalAmount () {
      return (this.total + this.sgstAmount + this.cgstAmount)
    }
  },
  methods: {
    proceed () {
      this.$refs.client.valid().then(valid => {
        if (valid) {
          return this.$refs.transaction.valid()
        }
      }).then(valid => {
        if (valid && this.$refs.items.valid()) {
          this.$emit('orderComplete', this.orderParams)
        }
      })
    },
    calculatePendingAmpount () {
      if (this.orderParams.pending_amount > 0) {
        this.pendingAmount -= this.amount
      } else {
        this.pendingAmount = this.totalAmount - this.amount
      }
    }
  },
  data () {
    return {
      params: {
        name: '',
        address: '',
        gst_number: '',
        line_items: [],
        transactions: [],
        sgst_percentage: 0,
        cgst_percentage: 0,
        pending_amount: 0
      },
      amount: 0,
      pendingAmount: 0,
      total: 0,
      client: {}
    }
  }
}
</script>



